.title{
  position: absolute;
  top: 15px;
  font-size: 45px;
  color: rgb(109, 98, 255);
  font-family: 'Roboto', sans-serif;
  
}
.app-container{
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  background: url(/weather-app/static/media/clouds.548144b9.jpg) center center / cover no-repeat;
  
}
.form{
  width: 400px;
  background: rgba(127, 154, 204, 0.5);
 border-radius: 4%;
  min-height: 100px;
 padding: 50px;
 margin-top: 15px;
}
.input{
  opacity: 0.9;
  width: 100%;
  height: 40px;
  border-radius: 10px;
  outline: none;
  border: none;
  font-family: 'Roboto', sans-serif;
  padding-left: 15px;
  
}
.btn{
  width: 80%;
  height: 40px;
 border-radius: 10px;
 outline: none;
 border: none;
 position: relative;
 top: 300px;
 font-family: 'Roboto', sans-serif;
}
.text{
  font-family: 'Roboto', sans-serif;
  margin-top: 65px;
}
.descr_city{
  font-size: 50px;
  margin-top: -10px;
}
.descr_temp{
  font-size: 55px;
}
.descr_min_max{
  font-size: 25px;  
}
.descr_hum{
  margin-top: 15px;
